.comments {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-direction: column;
    margin-left: 200px;
}

.commentsTitle {
    align-self: flex-start;
}

.commentsInput {
    width: 400px;
    height: 30px;
}

.commentsBtn {
    display: block;
    margin-top: 30px;
}

.comments button {
    height: 40px;
    background-color: rgba(8, 140, 160, 1);
    border: none;
    border-radius: 20px;
    outline: none;
    padding: 10px;
    color: rgba(255, 255, 255, 1);
    cursor: pointer;
}

.comments button:hover {
    color: rgba(255, 255, 255, .6);
    background-color: rgb(8, 168, 189);
}
.comment{
    display: grid;
    grid-row-gap: 10px;
    margin-top: 100px;
    padding: 20px;
    background-color: #b6b3b7;
}

.commentOne{
    display: flex;
    flex-direction: column;
}
.commentText{
    padding: 5px;
    width: 400px;
    background-color: #e7da86;
}

.commentLike{
    align-self: flex-end;
    margin-top: 5px;
    cursor: pointer;
}

.commentLike:hover{
    color: rgba(0,0,0,.5);
}

.emptyComment{
    color: #ffffff;
}